import {listing} from '../../api/api.js'
import {getAddress,deleteAddress,addAddress} from '../../api/request.js'
export default{
	data() {
		return {
			addressList: []
		}
	},
	methods:{
		//获取地址信息的网络请求
		getAdderss_method(){
			let data={
				userid:'111'
			};
			
			listing(getAddress)
			.then((res)=>{
				this.addressList=res.data.addressList;
			})
			.catch((err)=>{
				
			})
		},
		//删除地址
		deleteAddress_method(index){
			let data={
				userid:'111',
				address_id:'111'
			};
			listing(deleteAddress)
			.then((res)=>{
				this.addressList.splice(index,1)
			})
			.catch((err)=>{
				
			})
			
		},
		//增加地址
		addAddress_method(){
			uni.navigateTo({
			    url: './addSite'
			});
		},
		//修改本地地址并且返回到上一级地址
		changeAddress(item){
			//返回上一级别
			console.log("修改当前缓存地址")
			this.$store.commit("UpdataAddress",item)
			uni.navigateBack({
				
			})
		}
	},
	created() {
